<template>
	<div>
		<span class="back-btn" @click="$router.back()"></span>
		<div class="download-page">
			<div class="app-icon"></div>
			<p class="app-name">易视云</p>
			<div class="introduce" v-if="currentEnv=='IOS'">
				<p>您当前使用的是苹果系统，请于AppStore商店免费下载"易视云"<br>
					App试用账号:yuncun,密码:123456</p>
				<!-- https://apps.apple.com/cn/app/%E6%98%93%E8%A7%86%E4%BA%91/id1032277907#?platform=iphone -->
			</div>
			<div class="introduce" v-else>
				<p>App试用账号:yuncun,密码:123456</p>
				<a class="download-btn block-button" href="https://count.liqucn.com/d.php?id=1044394&amp;urlos=android&amp;vc_offset=1">点&nbsp;击&nbsp;下&nbsp;载</a>
			</div>
		</div>
	</div>
</template>
<script>
import axios from "axios"

export default {
	data() {
		return {}
	},
	components: {

	},
	methods: {

	},
	computed: {
		currentEnv: function() {
			if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
				return "IOS"
			} else if (/(Android)/i.test(navigator.userAgent)) {
				return "Android";
			} else {
				return "PC"
			};
		}
	}
}

</script>
<style scoped>
.back-btn{
	width: 50px;
	height: 50px;
	border-radius: 100%;
	position: fixed;
	top: 20px;
	left: 20px;
	background: #f3f3f3;
	box-shadow: 1px 1px 8px rgba(150,150,150,0.7);
}
.back-btn:after {
	content: "";
	position: absolute;
	top: 0rem;
	bottom: 0rem;
	margin: auto;
	left: 0;
	right: 0;
	width: 0.1rem;
	height: 0.1rem;
	border-top: 0.01rem #bbb solid;
	border-right: 0.01rem #bbb solid;
	transform: rotate(-135deg);
	-webkit-transform: rotate(-135deg);
}
.download-page {
	padding: 100px 20px 20px;
}

.app-icon {
	width: 125px;
	height: 125px;
	border-radius: 24px;
	margin: 20px auto 10px;
	background: url("../../assets/img/app_icon.png") center center no-repeat;
	background-size: 125px;
}

.app-name {
	text-align: center;
	font-size: 20px;
}

.download-btn {
	line-height: 50px;
	border-radius: 24px;
	font-size: 18px;
	font-weight: 500;
	box-sizing: border-box;
	background-image: linear-gradient(to right, #3e78c4, #3e78c4);
	color: #fff;
	margin-top: 10px;
}

.block-button {
	display: block;
	width: 80%;
	margin-left: auto;
	margin-right: auto;
}

.introduce {
	text-align: center;
	padding: 30px 20px 0px;
}

</style>



